$c--greyDark: #676767;
$c--white: #fff;
$c--blackLight: #191919;
$c--grey: #333;

.article {

  &__item {
    background-color: $c--white;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0,0,0.1);
    margin-bottom: 30px;
    overflow: hidden;
    transition: all .3s ease;
    transform: translateY(0);

    &:hover {
      box-shadow: 0 5px 15px rgba(0,0,0,.2);
      transform: translateY(-2px);
    }

    a {
      display: block;
      color: $c--grey;

      &:hover {
        text-decoration: none;
      }
    }
  }

  &__section {
    font-size: 0.75rem;
    margin: 5px 0 10px;
    color: $c--greyDark;

    span {
      padding: 2px 7px;
      background: #ddd;
      border-radius: 5px;
    }
  }

  &__date {
    margin-top: 20px;
    font-size: 0.75rem;
    color: $c--greyDark;
  }

  &__card {

    figure {
      margin-bottom: 0;
      position: relative;
      padding-bottom: 55%;
      overflow: hidden;
    }

    img {
      position: absolute;
      width: 100%;
    }

    h2 {
      display: block;
      display: -webkit-box;
      font-size: 1rem;
      font-weight: 600;
      margin-bottom: 0;
      line-height: 1.3;
      min-height: 60px;
      -webkit-line-clamp: 3;
      overflow: hidden;
      text-overflow: ellipsis;
      /* autoprefixer: off */
      -webkit-box-orient: vertical;
    }

    .article {

      &__desc {
        padding: 10px 15px;
      }
    }
  }

  &__list {

    figure {
      margin-bottom: 0;
      position: absolute;
      left: 20px;

      @media only screen and (max-width: 480px) {
        position: relative;
        left: 0;
      }
    }

    a {
      padding: 20px;
    }

    img {
      @media only screen and (max-width: 480px) {
        width: 100%;
      }
    }

    h2 {
      font-size: 1.3rem;

      @media only screen and (max-width: 480px) {
        font-size: 1rem;
      }
    }

    .article {

      &__item {
        box-shadow: 0px 0px 5px rgba(0,0,0,0);
        min-height: 180px;

        &:hover {
          box-shadow: 0 5px 15px rgba(0,0,0,.2);
        }
      }

      &__desc {
        padding-left: 230px;

        @media only screen and (max-width: 480px) {
          padding-left: 0;
          margin-top: 20px;
        }
      }

    }
  }
}

%loader-default {
  background: $c--blackLight;
  animation: load 1s infinite ease-in-out;
  width: 1em;
  height: 4em;
}

%loader-pseudo {
  position: absolute;
  top: 0;
  content: '';
}

.loader {
  color: $c--blackLight;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  animation-delay: -0.16s;
  @extend %loader-default;

  &:before {
    left: -1.5em;
    animation-delay: -0.32s;
    @extend %loader-pseudo;
    @extend %loader-default;
  }

  &:after {
    left: 1.5em;
    @extend %loader-pseudo;
    @extend %loader-default;
  }
}

@-webkit-keyframes load {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}

@keyframes load {
  0%,
  80%,
  100% {
    box-shadow: 0 0;
    height: 4em;
  }
  40% {
    box-shadow: 0 -2em;
    height: 5em;
  }
}